<root>
  <h2>Act</h2>
  <p>
    In this phase, you perform your target action (e.g. call your target method)
    and capture the result you want to verify (e.g. save the return value).
  </p>
  <p>
    Each unit test should test one thing. If you find yourself calling multiple
    target methods within a single test, consider breaking them up into multiple
    tests.
  </p>
  <p>
    If an unexpected error is raised (or if an expected error is not raised) it
    means there is a bug in the target method.
  </p>
</root>

