<script lang="ts">
  import type { SvelteComponent } from "svelte";
  import {
    getConnectionPoint,
    getPathData,
    relativeRect,
  } from "./connectionLines";
  import type { Connection, PageItem, Side } from "./types";

  const styleFromLayout2 = (layout: string) => {
    if (layout) {
      const parts = layout.split(/\s+/);
      const style = [];
      parts.forEach((part) => {
        if (part.startsWith("cs")) {
          style.push(`grid-column-end: span ${part.substring(2)}`);
        } else if (part.startsWith("c")) {
          style.push(`grid-column-start: ${part.substring(1)}`);
        } else if (part.startsWith("rs")) {
          style.push(`grid-row-end: span ${part.substring(2)}`);
        } else if (part.startsWith("r")) {
          style.push(`grid-row-start: ${part.substring(1)}`);
        }
      });

      return style.join(";");
    }
  };

  // ---------- public properties ---------- //
  export let pageItems: PageItem[];

  // ---------- refs ---------- //
  let root;
  let items = [];
  let clientWidth, clientHeight;

  // ---------- styles ---------- //
  $: layoutStyles = pageItems.map((pageItem) => styleFromLayout2(pageItem[1]));

  // ---------- measurements ---------- //
  $: rootRect =
    clientWidth && clientHeight && root && root.getBoundingClientRect();

  $: itemRects =
    clientWidth && clientHeight
      ? items.map((item) => {
          return (
            item &&
            rootRect &&
            relativeRect(rootRect, item.getBoundingClientRect())
          );
        })
      : [];

  // ---------- connection paths ---------- //
  const getSides = (connection: Connection): { from: Side; to: Side } => {
    switch (connection) {
      case "b-t":
        return { from: "bottom", to: "top" };
      case "b-l":
        return { from: "bottom", to: "left" };
      case "b-r":
        return { from: "bottom", to: "right" };
      case "l-t":
        return { from: "left", to: "top" };
      case "l-r":
        return { from: "left", to: "right" };
      case "r-l":
        return { from: "right", to: "left" };
      case "r-t":
        return { from: "right", to: "top" };
      default:
        return { from: "bottom", to: "top" };
    }
  };

  $: connections = pageItems.map(
    (pageItem) => pageItem.length === 3 && pageItem[2]
  );

  $: paths = connections.map((connection, index, array) => {
    if (connection && itemRects[index] && itemRects[index + 1]) {
      const sides = getSides(connection);
      const fromPoint = getConnectionPoint(itemRects[index], sides.from);
      const toPoint = getConnectionPoint(itemRects[index + 1], sides.to);
      return getPathData(fromPoint, sides.from, toPoint, sides.to);
    }
    return undefined;
  });
</script>

<page-root bind:clientWidth bind:clientHeight bind:this={root}>
  {#if clientWidth >= 900}
    <content-layer>
      <connections>
        <svg>
          {#each paths as d}
            {#if d}
              <path {d} stroke="#444" stroke-width="10" fill="transparent" />
            {/if}
          {/each}
        </svg>
      </connections>
      <content-grid>
        {#each pageItems as pageItem, index}
          <content-item bind:this={items[index]} style={layoutStyles[index]}>
            <svelte:component this={pageItem[0]} />
          </content-item>
        {/each}
      </content-grid>
    </content-layer>
  {:else}
    <linear-content>
      {#each pageItems as pageItem, index}
        <linear-content-item>
          <svelte:component this={pageItem[0]} />
        </linear-content-item>
      {/each}
    </linear-content>
  {/if}
</page-root>

<style>
  page-root {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 900px;
    margin: 0 auto;
  }

  content-layer {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    place-content: stretch;
    place-items: stretch;
    max-width: 900px;
    margin: 0 auto;
  }

  content-grid {
    display: grid;
    grid-template-columns: repeat(13, 1fr);
    grid-template-rows: repeat(5, minmax(50, auto));
    place-items: stretch;
    place-content: stretch;
    grid-column: 1;
    grid-row: 1;
    row-gap: 50px;
  }

  connections {
    grid-column: 1;
    grid-row: 1;
    width: 100%;
    height: 100%;
  }

  connections svg {
    width: 100%;
    height: 100%;
  }

  content-item {
    background: white;
    border: 1px solid #333;
    padding: 10px;
    display: flex;
    height: fit-content;
    flex-direction: column;
    align-content: stretch;
    align-items: stretch;
    justify-content: flex-start;
    justify-items: flex-start;
  }

  linear-content {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  linear-content-item {
    background: white;
    padding: 10px;
    display: flex;
    flex-direction: column;
    place-content: stretch;
    place-items: stretch;
  }
</style>
