<root>
  <p>
    Running code takes action. The voice of comments should reflect
    activity. Use verbs associated with the action or decision you made in
    the code.
  </p>
  <p>
    Passive voice has repetative extra words and is harder to read. You may
    need passive voice when writing your next novel, but less often in
    comments.
  </p>
</root>

