<root>
  <h3>Benefits</h3>
  <ul>
    <li>The test name is easy to read as a meaningful sentence.</li>
    <li>
      The descriptive name avoids the need for comments to explain what the test
      does.
    </li>
    <li>
      The name encourages asserting one logical result per condition. This makes
      the test more atomic and less likely to partial failures that require
      stepping through with the debugger.
    </li>
    <li>
      The test is easy to distinguish from other tests. Any decent test runner
      shows you where the test lives in the code hierarchy (e.g. project,
      namespace, and class) so putting code structure in the name is
      unnecessary.
    </li>
  </ul>
</root>

