<script lang="ts">
  import { onMount } from "svelte";
  import { goToPage, showPage } from "./dispatch";
  import { currentPage } from "./stores";
  import AppHeader from "./AppHeader.svelte";
  import About from "./newFrontPage/About.svelte";
  import BlurbGrid from "./newFrontPage/BlurbGrid.svelte";

  let clientWidth: number;

  const onBack = () => {
    const urlParams = new URLSearchParams(window.location.search);
    showPage(urlParams.get("id"));
  };

  onMount(() => {
    const urlParams = new URLSearchParams(window.location.search);
    goToPage(urlParams.get("id"));
  });
</script>

<svelte:head>
  {#if $currentPage}
    <title>{$currentPage.title || "geoffcox.github.io"}</title>
  {/if}
</svelte:head>

<svelte:window on:popstate={onBack} />
<!--
  @component
  The demo application
-->
<app
  bind:clientWidth
  class:cozy={clientWidth >= 350 && clientWidth < 650}
  class:compact={clientWidth < 350}
>
  <app-header>
    <AppHeader />
  </app-header>
  <app-content>
    {#if $currentPage}
      <page>
        <svelte:component this={$currentPage.component} />
      </page>
    {:else}
      <front-page>
        <about>
          <About />
        </about>
        <blurb-grid>
          <BlurbGrid />
        </blurb-grid>
      </front-page>
    {/if}
  </app-content>
  <app-footer />
</app>

