<div>
  <p>
    Flow is when you are entirely focused on your current work. You tune out
    distraction, stop watching the clock, and make meaningful progress.
  </p>
  <p>
    Practicing concentration techniques and having a consistent cadence for
    your day trains your brain to quickly get into deep flow.
  </p>
  <p>
    Train others to not interrupt you when you are thinking. Set a busy
    status on your computer, turn off notifications, and consider a sign or
    light that indicates you are not available.
  </p>
</div>
