<div>
  <h2>Communicate with accountability</h2>
  <ul>
    <li>Commit to completing specific, atomic tasks each day.</li>
    <li>Prefer action verbs for tasks like 'create', 'update', or 'fix'.</li>
    <li>Be prepared to give status to your team at any time.</li>
    <li>
      Call yourself out when you don't get something done or make a mistake.
    </li>
    <li>
      Avoid excuses for failures. State how you will do better in the future.
    </li>
    <li>Don't be afraid to ask for help when blocked.</li>
  </ul>
</div>
