<root>
  <h3>Benefits</h3>
  <ul>
    <li>
      It is clear what is being tested vs. what is setup and verification.
    </li>
    <li>It is easier to debug errors arranging the test vs. the target.</li>
    <li>
      You are less likely to accidentally test a mocked function. It happens
      more than you might expect.
    </li>
    <li>
      You are more likely to factor unit tests to test one thing at a time.
    </li>
  </ul>
</root>

