<div>
  <h2>Plan each week in solitude</h2>
  <ul>
    <li>Take 30 minutes on Monday, alone, to frame your week.</li>
    <li>
      Pick a theme for the week. Consider your team's goals for the current
      period.
    </li>
    <li>
      Block out time on your calendar to get work done, learn, and to rest
      between major efforts.
    </li>
  </ul>
</div>
