<root>
  <h2>Assert</h2>
  <p>
    In this phase, you verify the result from the target method matches the
    expected result.
  </p>
  <p>
    Most test frameworks include helper methods letting you compare values and
    fail the test if they don't match.
  </p>
  <p>
    Unit test purists want each unit test to verify one thing per test. That's a
    perfectly OK practice if you want to factor tests that way.
  </p>
</root>

