<root>
  <p>
    Comments that explain what the code is doing are only valuable to those
    unfamiliar with the language. The code itself describes exactly what will
    happen when it runs. This precision is the entire point of programming
    lanugages (vs. natural languages)
  </p>
  <p>
    Adding <i>what</i> comments may be a habit created when learning to program.
    Students often pseudo-code the sequence of statements in comments. It is easy
    to forget to clean them up after the code is written.
  </p>
</root>

