<root>
  <p>
    Keep in mind comments are for humans, not for computers. You want to get the
    highest ink-to-value ratio possible. Comments should only be added where
    they provide value to the reader.
  </p>
  <p>
    Don't use tools that auto-generate comments in your source. If you <i
      >must</i
    >
    publish online documentation and every single thing <i>must</i>
    be documented to meet a conformance requirement, see if your build system can
    generated missing comments at build time.
  </p>
</root>

<style>
  root {
    width: fit-content;
    margin: auto;
  }

  p {
    max-width: 1.82in;
  }
</style>
