<root>
  <h3>Tips</h3>
  <ul>
    <li>
      Use the past tense of verbs. Past tense solidifies the test as a factual
      entity in the readers mind.
    </li>
    <li>
      Avoid passive verbs (e.g. is, are). They take up space without much
      benefit and humans reading the test name will fill these in automatically
      in their heads.
    </li>
    <li>
      Choose nouns and verbs you would use when describing the test to other
      developers (e.g. set a property, call a method, raise an event).
    </li>
    <li>
      Don't worry too much about name length, but keep the names just long
      enough to distinguish one test from another and make the intention of the
      test clear.
    </li>
  </ul>
</root>

<style>
  h3 {
    margin-block-start: 0.5em;
    margin-block-end: 0.5em;
  }
</style>
