<script lang="ts">
  import type { Blurb } from "../types";
  import HorizontalBlurbCard from "./HorizontalBlurbCard.svelte";
  import VerticalBlurbCard from "./VerticalBlurbCard.svelte";
  import SmallBlurbCard from "./SmallBlurbCard.svelte";

  export let blurb: Blurb;

  let clientWidth: number;
</script>

<!--
    @component
    A blurb card helper with a layout based on the blurb.layout and space constraints.
  -->
<blurb-card bind:clientWidth>
  {#if clientWidth < 390}
    <SmallBlurbCard {blurb} />
  {:else if blurb.layout === "1A"}
    <HorizontalBlurbCard {blurb} />
  {:else if blurb.layout === "1B"}
    <HorizontalBlurbCard {blurb} contentBeforeImage />
  {:else if blurb.layout === "1C"}
    <VerticalBlurbCard {blurb} />
  {:else if blurb.layout === "1D"}
    <VerticalBlurbCard {blurb} contentBeforeImage />
  {:else}
    <HorizontalBlurbCard {blurb} />
  {/if}
</blurb-card>

<style>
  blurb-card {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    height: 100%;
    place-items: stretch;
    place-content: stretch;
    width: 100%;
  }
</style>
