<root>
  <h3>Tips</h3>
  <ul>
    <li>
      If you have multiple variables for expected or actual values, then use <i
        >expected</i
      >
      and <i>actual</i> as prefixes (e.g. expectedFirstName, expectedLastName).
    </li>
    <li>
      If you are testing a static method and don't have an instance variable for
      your target you can just skip naming anything target.
    </li>
    <li>
      If your language can infer types, then don't specify the types of
      variables explicitly (e.g. use var, let, const). If you need to verify a
      type, then assert you have the correct type.
    </li>
  </ul>
</root>

<style>
  h3 {
    margin-block-start: 0.5em;
    margin-block-end: 0.5em;
  }
</style>
