<root>
  <h3>Benefits</h3>
  <ul>
    <li>Helps ensure your assertion compares expected vs. actual.</li>
    <li>
      Improved readability of tests. Other developers can scan quickly to find
      the key variables.
    </li>
    <li>
      You avoid depending on the types and names of the code under test. If that
      code changes some names, you don't have to update all your tests.
    </li>
  </ul>
</root>

