<root>
  <h2>Arrange</h2>
  <p>In this phase, you set up the prerequisite state for you test.</p>
  <p>
    This includes preparing input arguments to pass to your target method and
    sometimes mocking dependencies used by your target method.
  </p>
  <p>
    If an error is raised, it means the test itself is invalid and not that code
    under test failed.
  </p>
</root>

