<div>
  <h2>Practice Flow</h2>
  <ul>
    <li>
      Try techniques like pomodoro. Set 20 minute timers to focus on one
      things at a time.
    </li>
    <li>Create a ritual cadence for your day.</li>
    <li>Be unavailable for distraction when you are concentrating.</li>
    <li>
      Avoid checking email or text messages first thing. Instead jump into
      flow first.
    </li>
  </ul>
</div>
