import type { Point, Rect, Side } from "./types";

const getMidpoint = (v1: number, v2: number) => {
  if (v1 > v2) {
    return v2 + (v1 - v2) / 2;
  }
  return v1 + (v2 - v1) / 2;
};

export const relativeRect = (
  parentRect: DOMRect,
  childRect: DOMRect
): DOMRect => {
  return DOMRect.fromRect({
    x: childRect.x - parentRect.x,
    y: childRect.y - parentRect.y,
    width: childRect.width,
    height: childRect.height,
  });
};

export const getConnectionPoint = (rect: Rect, side: Side) => {
  const { x, y, width, height } = rect;
  switch (side) {
    case "top":
      return {
        x: x + width / 2,
        y,
      };
    case "right":
      return {
        x: x + width,
        y: y + height / 2,
      };
    case "bottom":
      return {
        x: x + width / 2,
        y: y + height,
      };
    case "left":
    default:
      return {
        x: x,
        y: y + height / 2,
      };
  }
};

export const getPathData = (
  fromPoint: Point,
  fromSide: Side,
  toPoint: Point,
  toSide: Side
) => {
  const d = [];
  d.push(`M ${fromPoint.x},${fromPoint.y}`);

  switch (fromSide) {
    case "top":
      break;
    case "right":
      switch (toSide) {
        case "top":
          d.push(`L ${toPoint.x},${fromPoint.y}`);
          break;
        case "right":
        case "bottom":
        case "left":
          const midX = getMidpoint(fromPoint.x, toPoint.x);
          d.push(`L ${midX},${fromPoint.y}`);
          d.push(`L ${midX},${toPoint.y}`);
          break;
      }
      break;
    case "bottom":
      switch (toSide) {
        case "top":
          const midY = getMidpoint(fromPoint.y, toPoint.y);
          d.push(`L ${fromPoint.x},${midY}`);
          d.push(`L ${toPoint.x},${midY}`);
          break;
        case "right":
          d.push(`L ${fromPoint.x},${toPoint.y}`);
          break;
        case "bottom":
          break;
        case "left":
          d.push(`L ${fromPoint.x},${toPoint.y}`);
          break;
      }
      break;
    case "left":
      switch (toSide) {
        case "top":
          d.push(`L ${toPoint.x},${fromPoint.y}`);
          break;
        case "right": {
          const midX = getMidpoint(fromPoint.x, toPoint.x);
          d.push(`L ${midX},${fromPoint.y}`);
          d.push(`L ${midX},${toPoint.y}`);
          break;
        }
        case "bottom":
        case "left":
        default:
          break;
      }
      break;
    default:
      break;
  }

  d.push(`L ${toPoint.x},${toPoint.y}`);
  return d.join(" ");
};
